        "description": "\n子どもたちの豊かな心を育てていくために、小さなときから絵本にふれることはとても大切です。下新倉分館では、毎月第2土曜日に子どもたちに読み聞かせをしています。ぜひ、親子ご一緒にいらしてください。\n　日時：9月9日（土）11時～\n　場所：図書館下新倉分館　キッズスペース\n　対象：幼児～小学校低学年\n\n↑ 7月8日の下新倉分館での絵本とおはなしの会の様子\n",
