        "id": "0c6e60061c38df8dd985c95b711bc43c",
        "description": "\n絵本であかちゃんとのふれあいを！絵本はあかちゃんとの楽しい時間をつくります。事前にお申込みの上、あかちゃんと一緒にお越しください。お待ちしております。\nブックスタートとは？\n和光市ブックスタート事業予定表（令和5年度）.pdf(PDF:363.4KB)\n本館\n９/4（月）①10：00～10：30　②10：45～11：15　③11：30～12：009/25（月）①10：00～10：30　②10：45～11：15　③11：30～12：0010/2（月）①10：00～10：30　②10：45～11：15　③11：30～12：00\n下新倉分館\n10/24（火）11：00～11：30\n対象日の参加が難しいまたは対象日に参加できなかった方\n和光市在住で2020年4月以降にお生まれのお子さまは、別日に参加可能です。（要申込み）\n申込み方法\n子育て世代包括支援センター・総合児童センター実施回へのお申込みも図書館までお願いいたします。\n１．電話和光市図書館　048-463-8723下新倉分館　　048-452-6011\n２．メール　h0301★city.wako.lg.jp※★を@に変更し①お子さまの氏名（ふりがな）②生年月日③希望日時④連絡先を入力の上、送信してください。＜メール送信用QRコード＞\n3．LINE詳しくはこちらをご覧ください。\n当日の持ち物\n\n\n母子健康手帳\n\n\n\n\n絵本を持ち帰るための袋\n\n\n留意事項\n天候等により中止になる場合があります。その場合は、図書館ホームページにてお知らせいたします。\n",
        "location": "和光市図書館",
        "fee": "詳細から確認をお願いします",
