        "title": "和光図書館【分館】7/11（火）～8/27（日）「図書館でみつけよう！夏休み自由研究」",
        "detail_link": "https://www.wakolib.jp/eventnews/712828.html",
        "id": "3b626f6344c00ce7d86c7798e7d1c958",
        "description": "\n下新倉分館では、夏休みの自由研究に役立つ20種類のアイデアカード「自由研究のタネ」をご用意しています。工作、実験、和光市にちなんだものなど、テーマごとに研究の方法と参考になる本を紹介しています。\nアイデアカードは自由にお持ち帰りいただけます。宿題のためだけではなく、夏休みを楽しく過ごす「タネ」としても、ぜひ使ってください。\n\n同時企画：「体験してみよう！図書館司書のおしごと」　\n　　　　　　小学生の職業体験を募集します。\n　　　　　　貸出・返却・配架・本探し・修理・POP作成など、司書の仕事内容を\n　　　　　　体験していただけます。\n　日　時　／令和5年7月21日(金)～8月18日(金)の水・木・金曜日のいずれか1日。\n　　　　　　1人1回(コースが同じなら同時刻に2名まで受付)\n　　　　　　《Aコース》4～6年生向け／午前10：00～11：30(90分)\n　　　　　　《Bコース》1～3年生向け／午前10：00～11：20(80分)\n　場　所　／和光市図書館下新倉分館\n　　　　　　TEL：048－452－6011\n　申込方法／下新倉分館へ電話または窓口にてお申し込みください。\n　　　　　　7/11(火)より受付いたします。\n \n\n司書体験の申込書はこちらからダウンロードできます。\n2023司書体験申込要綱.pdf(PDF:76.4KB)\n",
