        "title": "和光図書館【やさしいにほんご】ブックスタート（ぶっくすたーと）の　よやく",
        "detail_link": "https://www.wakolib.jp/eventnews/post_40.html",
        "id": "e14871d8b82e1219baf77171054296f6",
        "description": "\nブックスタート（ぶっくすたーと）とは？\n絵本（えほん）があると　あかちゃんと　たのしい時間（じかん）が　すごせます。あかちゃんが　たのしく　しあわせに　すごせることを　ねがって、１さつずつ　絵本（えほん）を　プレゼント（ぷれぜんと）します。\n絵本を　よんで　きかせたり、　日本で　むかしから　うたわれている子（こ）どもの　うたを　おしえます。\n※お金（かね）は　かかりません。※日本語（にほんご）で　せつめいします。\n＜ポスター（ぽすたー）＞　※PDFファイル（ぴーでぃーえふ　ふぁいる）が　ひらきます。\n2023ねん5がつ～10がつのブックスタート（やさしいにほんご）.pdf(PDF:433.7KB)\n2023ねん10がつ～2024ねん3がつのブックスタートのポスター（やさしいにほんご）.pdf(PDF:425.1KB)\n参加（さんか）できる人（ひと）\n\n和光市（わこうし）に　すんでいる人（ひと）\n2020年（ねん）4月（がつ）より　まえに　生（う）まれた　あかちゃんがいる人（ひと）\n\n※１．２．りょうほうに　あてはまる人（ひと）が　参加（さんか）できます。※絵本（えほん）は、　あかちゃん　1人（ひとり）につき　１さつ　わたします。\n日（ひ）にちと　場所（ばしょ）\n\n\n\n\nNo.\n\nひにち\nじかん\nばしょ\nあかちゃんがうまれた月（つき）\n\n\n1～6は　おわりました\n\n\n7\n5月23日(火)\n11:00～11:30\n分館\n2022年11月～2023年1月生まれ\n\n\n8\n6月5日(月)\n10:00～10:30\n本館\n2023年1月生まれ\n\n\n9\n6月5日(月)\n10:45～11:15\n本館\n2023年1月生まれ\n\n\n10\n6月5日(月)\n11:30～12:00\n本館\n2023年1月生まれ\n\n\n11\n6月12日(月)\n10:00～10:45\n南\n2020年4月よりあと　生まれ\n\n\n12\n6月26日(月)\n10:00～10:45\n児童\n2020年4月よりあと　生まれ\n\n\n13\n7月3日(月)\n10:00～10:30\n本館\n2023年2月生まれ\n\n\n14\n7月3日(月)\n10:45～11:15\n本館\n2023年2月生まれ\n\n\n15\n7月3日(月)\n11:30～12:00\n本館\n2023年2月生まれ\n\n\n16\n7月25日(火)\n11:00～11:30\n分館\n2023年2・3月生まれ\n\n\n17\n9月4日(月)\n10:00～10:30\n本館\n2023年3月生まれ\n\n\n18\n9月4日(月)\n10:45～11:15\n本館\n2023年3月生まれ\n\n\n19\n9月4日(月)\n11:30～12:00\n本館\n2023年3月生まれ\n\n\n20\n9月25日(月)\n10:00～10:30\n本館\n2023年4月生まれ\n\n\n21\n9月25日(月)\n10:45～11:15\n本館\n2023年4月生まれ\n\n\n22\n9月25日(月)\n11:30～12:00\n本館\n2023年4月生まれ\n\n\n\n※本館（ほんかん）の　場所（ばしょ）は　こちらです。＜GoogleMapが　ひらきます＞\n※分館（ぶんかん）の　場所（ばしょ）は　こちらです。＜GoogleMapが　ひらきます＞\n※１１の「南（みなみ）」は、「南子育て世代包括支援センター（みなみ　こそだてせだい　ほうかつしえん　せんたー）」で　おこないます。　場所（ばしょ）は　こちらです。＜GoogleMapが　ひらきます＞\n※１２の「児童（じどう）」は、「和光市総合児童センター（わこうし　そうごう　じどうせんたー）」で　おこないます。　場所（ばしょ）は　こちらです。＜GoogleMapが　ひらきます＞\n２３より　あとの日（ひ）にちは、　9月（がつ）に　ホームページ（ほーむぺーじ）に　のせます。　早（はや）く　知（し）りたい人（ひと）は　ポスター（ぽすたー）を　見（み）てください。\n2023ねん10がつ～2024ねん3がつのブックスタート（やさしいにほんご）.pdf(PDF:425.1KB)\nよやくの　しかた\n下（した）の　よやくボタン（ぼたん）を　おすと　メールソフトが　ひらきます。\n\n\nよやくしたい日（ひ）の　番号（ばんごう）\nあかちゃんの　名前（なまえ）\nあかちゃんの生（う）まれた日（ひ）\nメールアドレス（めーるあどれす）\n\nを入力（にゅうりょく）して、　メール（めーる）を　おくってください。３日（にち）以内（いない）に　図書館（としょかん）から　メール（めーる）で　れんらくします。\nもってきて　ほしいもの\n\n母子健康手帳（ぼし　けんこう　てちょう）\n\n絵本（えほん）を　入（い）れる　かばん\n\n\n\nあかちゃんも　いっしょに　来（き）てください。\n\n",
