        "id": "174be4396db4840507e9bb0db55e4582",
        "description": "\nいつも大人気の坂口美佳子先生の「子どもの科学」が、今年度も開催されます。\n・日時：６月１７日（土）１４時００分～１５時３０分\n・場所：和光市図書館本館　会議室\n・お話をしてくれる人：坂口　美佳子先生（科学読物研究会）\n・内容：コマについて学び、ブンブンゴマを作る。\n・定員：市内在住の小学生　２０名（抽選制）\n・申込：往復葉書に以下のことを明記して、和光市図書館本館（〒351-0114　和光市本町３１－１）まで郵送してください。（詳細は以下のチラシ参照）\n　　　　①郵便番号　②住所　③名前（ふりがなつき）④電話番号　⑤学校名　⑥学年\n　　　　6月４日（日）必着。　\n【チラシ】第１回子どもの科学（ＵＦＯブンブンゴマをつくろう） .pdf(PDF:601.4KB)\n※6月5日（月）以降に到着した葉書につきましては抽選の対象外となりますので、ご注意ください。\n\n2月18日に開催された前回の「子どもの科学」の様子です。「化石」について学びました。\n\n",
